@media(max-width: 700px){
    .follower-img-1{
        flex-shrink: 0;
        visibility: hidden;
    }
}
body{
    margin: 0px;
}
hr{
    opacity: 0.5;
}
.main-header{
    display: block;
    background-image: linear-gradient(to right, #B2D8FF , #f3cb99);
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 1.5%;
    margin: 0px;
    text-align: right;
}
.header-btn{
    padding-top: 8px;
    padding-bottom: 8px;
    margin-right: 2%;
    background-color: #33adff;
    border: 0px;
    font-size: 16px;
    min-width: 7%;
    color: #fff;
}
.logo-img{
    max-width: 200px;    
}
.main-container{
    margin: 2.5%;
    color: #4D3C3C;
}
.sub-container{
    display: flex;
}
.topic{
    font-size: 18px;
    flex: left;
    display: flex;
    flex: 6;
    flex-flow: column wrap;
    

}
.topic-follower{
    display: flex;
    flex: 2;
    padding: 10px;
    overflow: hidden;
    flex: left;
}

.topic-data-1{
    flex: 1;
    padding: 5px;
    margin: auto;
}
.topic-data-2{
    flex: 1;
    padding: 5px;
    margin: auto;
}
.topic-data-3{
    flex: 1;
    padding: 5px;
    margin: auto;
    align-self: center;
}

.topic-footer{
    display: flex;
    font-size: 16px;
    opacity: 0.8;
}
.topic-img{
    flex: 1;
    margin: 4px;
    min-width: 12.5px;
    height: 12.5px;
}

.topic-img-1{
    background-color: green;
}
.topic-img-2{
    background-color: blue;
}
.topic-img-3{
    background-color: red;
}
.topic-img-4{
    background-color: purple;
}
.topic-img-5{
    background-color: crimson;
}
.topic-category{
    flex: 50;
    margin: auto;
}
.follower-img{
    flex-shrink: 0;
    width: 30px;
    height: 30px;
    padding: 5px;
    align-self: center;
}

.follower-img-1{
    flex-shrink: 0;
    width: 30px;
    height: 30px;
    padding: 5px;
    align-self: center;
}
.topic-description{
    text-align: justify;
}
    

